@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Open Sans';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior: smooth;
}

body.home{
  background-color: black;
}

.home header *{
  display: inline-block;
  vertical-align: middle;
  margin: 1vh 1vh;
}

.home h1{
  font-size: 5vh;
}
.home h2{
  font-size: 2.5vw;
  margin-top: 0px;
}

#logito{
  width: 5vw;
}

.home header{
  position: absolute;
  top: 0px;
  width: 100%;
  background-color: rgba(230, 230, 230, 0.4);
  color: white;
}

.home header a:hover {
  background-color: rgba(255, 255, 255, 0.4);

}
.header-right{
  margin: auto;
}


a{
    text-decoration: none;
}
.home header a {
  vertical-align: middle;
  float: left;
  color: white;
  text-align: center;
  padding: 4vh 1vh 4vh 1vh;
  font-size: 2vw;
  line-height: 1.5vh;
  border-radius: 4px;
}



#banner{
  max-height: 70vw;
  height: 60vh;
  overflow: hidden;
  position: relative;
}

.centered{
  position: absolute;
  top: 50%;
  left: 50%;
  color: white ;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 25px;
  font-size: 30px;
  padding: 10px;
}

#bannerimg{
  margin-top:0vw;
  width: 100%;
  opacity: 0.5;
}
.home section{
  padding-top:0;
  margin-top: 0px;
}


.cuadro{
  padding: 0px !important;
  overflow: hidden;
  max-height: 400px;
  border-radius: 20px;
}

.cuadro img{
  width: 100%;
}
.home h3{
  font-size: 1.5vw;
}

.gridcont{
  padding-top:7vh;
  margin: auto auto 7vh auto;
  max-width: 80%;
}
.gridcont *{
  color: white;
  background: none;
}
.valores{
  width: 50%;
}

.gridcont p{
  font-size: 2.5vh;
  text-align: justify;
  padding: 1vw;
}

#upbutton{
  color: white;
  position: fixed;
  width: 30%;
  max-width: 100px;
  bottom: 5%;
  right: 5%;
  font-size: 4vw;
}
@media screen and (max-width: 1200px){
  .gridcont p{
      font-size: 2vh;
  }
  h1.home{
    font-size: 5vw;
  }
  h2.home{
    font-size: 3vw;
  }
  h3.home{
    font-size: 2vw;
  }

  header.home a{
      font-size: 4vw;
      padding: 4vh 1vh 4vh 1vh;
  }

  #upbutton{
    max-width: none !important;
    font-size: 7vw;
  }
}


@media screen and (max-width: 1000px){
  .centered{
      font-size: 3vw;
    max-height: 200px;
    top: 70%;
  }
}


@media screen and (max-width: 540px){
    .home h2,.home h3{
      font-size: 4vw;
    }

    #slogan p{
      font-size: 3vw;
    }
    .gridcont{
      padding-top: 2vh;
      margin: auto auto 2vh auto;
    }

    header.home a{
        font-size: 4vw;
        padding: 2vh 1vh 2vh 1vh;
          line-height: 0.5vh;
    }
    .MuiGrid-grid-xs-6 {
      max-width: 100% !important;
      flex-basis: 100% !important;
    }

    .MuiGrid-grid-xs-true{
      max-width: 100% !important;
      flex-basis: 100% !important;
    }
    #upbutton{
      font-size: 15vw;
    }

    .header-right{
      margin: 0px !important;
    }

}
.form-content {
    border-radius: 0 10px 10px 0;
    position: relative;
    background: linear-gradient(90deg, rgb(40, 40, 40) 0%, rgb(17, 17, 17) 100%);  
    top:-50px;
  }
  .form {
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .form h1 {
    font-size: 1rem;
    text-align: start;
    width: 80%;
    margin-bottom: 1rem;
    color: #fff;
    text-align: center;
  }
  
  .form-inputs {
    margin-bottom: 0.5rem;
    width: 80%;
  }
  
  .form-inputs p {
    font-size: 0.8rem;
    margin-top: 0.5rem;
    color: #f00e0e;
  }
  
  .form-label {
    display: inline-block;
    font-size: 0.8rem;
    margin-bottom: 6px;
    color: #fff;
  }
  
  .form-input {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 2px;
    height: 40px;
    width: 100%;
    border: none;
  }
  .form-textarea {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 2px;
    height: 100px;
    width: 100%;
    border: none;
    resize:none;
  }
  
  .form-input-btn {
    width: 80%;
    height: 50px;
    margin-top: 10px;
    border-radius: 2px;
    background: linear-gradient(
      90deg,
      rgb(39, 176, 255) 0%,
      rgb(0, 232, 236) 100%
    );
    outline: none;
    border: none;
    color: #fff;
    font-size: 1rem;
  }
  .wrong{
    color: red;
  }

  .form-input-btn:hover {
    cursor: pointer;
    background: linear-gradient(
      90deg,
      rgb(39, 143, 255) 0%,
      rgb(45, 230, 230) 100%
    );
    transition: all 0.4s ease-out;
  }
  .form-sucess{
    color:aliceblue;
    align-items: center;
    text-align: center;
    font-size:150%;
    font-weight: bolder;
    margin-top:21px;
  }
  .form-img-2{
    width:80%;
    margin: 5% auto;
    display:flex;
  }
.drawer {
    height: 700px;
    overflow-y: scroll;
    background: #ffffff;
    top: 50px;
    
    background: rgba(255, 255, 255, 0.65);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 8.5px );
    -webkit-backdrop-filter: blur( 8.5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    
  }
  
  ::-webkit-scrollbar {
    width: 15px;
  }
  ::-webkit-scrollbar-track {
    background-color: #ffffff;
  }
  ::-webkit-scrollbar-thumb {
    background-color: rgb(179, 179, 179);
  }
  
  .sidebarWrapper {
    padding: 20px;
  }
  
  .sidebarList {
    padding: 0;
    margin: 0;
    list-style: none;
  }
  
  .sidebarListItem {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .sidebarIcon {
    margin-right: 15px;
  }
  
  .sidebarButton {
    width: 150px;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-weight: 500;
  }
  
  .sidebarHr {
    margin: 20px 0;
  }
  
  .sidebarFriendList {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  .profileImg {
    border-radius: 50%;
    width: 120px;
    height: 120px;
  }
  
.post {
    width: 100%;
    border-radius: 10px;
    box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
    margin: 30px 0;
  }
  
  .postWrapper {
    padding: 10px;
  }
  
  .postTop {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .postTopLeft {
    display: flex;
    align-items: center;
  }
  
  .postProfileImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .postUsername {
    font-size: 15px;
    font-weight: 500;
    margin: 0 10px;
  }
  
  .postDate{
      font-size: 12px;
  }
  
  .postCenter{
      margin: 20px 0;
  }
  
  .postImg{
      margin-top: 20px;
      width: 100%;
      max-height: 500px;
      object-fit: contain;
  }
  
  .postBottom{
      display: flex;
      align-items: center;
      justify-content: space-between;
  }
  
  .postBottomLeft{
      display: flex;
      align-items: center;   
  }
  
  .likeIcon{
      width: 24px;
      height: 24px;
      margin-right: 5px;
      cursor: pointer;
  }
  
  .postLikeCounter{
      font-size: 15px;
  }
  
  .postCommentText{
      cursor: pointer;
      border-bottom: 1px dashed gray;
      font-size: 15px;
  }
  
